import { getProfile, followUser, unFollowUser } from '@/api/profile'
import { getArticles } from '@/api/article'
import List from '../components/List'
import { mapState } from 'vuex'
export default {
    middleware: 'auth',
    name: 'UserProfile',
    components: {
        List
    },
    async asyncData({query}) {
        const page = parseInt(query.page || 1)
        const limit = 10
        const { username, tab } = query
        const [ articleRes, profileRes ] = await Promise.all([
            getArticles({
                limit: limit,
                offset: (page - 1) * limit,
                author: tab ? null : username,
                favorited: tab === 'favorited' ? username : null
            }),
            getProfile(query.username)
        ])
        const { articles, articlesCount } = articleRes.data
        const { profile } = profileRes.data

        articles.forEach(article => {
            article.favoriteDisabled = false
        })

        return {
            articles,
            articlesCount,
            limit,
            page,
            tab,
            profile
        }
    },
    watchQuery: ['page', 'tab'],
    computed: {
        ...mapState(['user']),
        totalPage () {
            return Math.ceil(this.articlesCount / this.limit)
        }
    },
    methods: {
        async followAuthor () {
            const username = this.profile.username
            if (this.profile.following) {
                await unFollowUser(username)
            } else {
                await followUser(username)
            }
            this.profile.following = !this.profile.following
        }
    }
}
